let mocha = require('mocha')
let chai = require('chai')
import * as matrixUtil from '../matrixUtil.js'

chai.should()

mocha.describe('matrixUtil', () => {
  mocha.describe('createEmptyArray()', () => {
    let a = matrixUtil.createEmptyArray(10)
    a.length.should.equal(10)
    a.every(element => element === 0).should.be.true
  })
})



